import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { Toast } from '@ionic-native/toast/ngx';
import { FormBuilder, FormGroup } from '@angular/forms';

import { UserSandbox } from '@sandboxes/user.sandbox';
import { CatalogSandbox } from '@sandboxes/catalog.sandbox';

@Component({
  selector: 'page-new-catalog',
  templateUrl: './new-catalog-page.component.html',
  styleUrls: ['./new-catalog-page.component.scss']
})
export class NewCatalogPage implements OnInit, OnDestroy {

  subscription;
  newCatalogForm: FormGroup;

  constructor(public router: Router,
              // private toast: Toast,
              private formBuilder: FormBuilder,
              private catalogSandbox: CatalogSandbox,
              public userSandBox: UserSandbox) {
    this.newCatalogForm = this.formBuilder.group({
      name: ['']
    });
  }

  ngOnInit() {
    this.subscription = this.catalogSandbox.catalogErrorMessage$.subscribe((error: string) => {
      if (error) {
        // this.toast.show(error, '3000', 'center');
      }
    });
  }

  ngOnDestroy() {
    this.subscription.unsubscribe();
  }

  onCreate() {
    this.catalogSandbox.createNewCatalog(this.newCatalogForm.value['name']);
    const subscription = this.catalogSandbox.catalogLoaded$.subscribe((loaded: boolean) => {
      if (loaded) {
        this.router.navigateByUrl('/app/tabs/catalogs');
      }
    });
  }

  onCancel() {
    this.router.navigateByUrl('/app/tabs/catalogs');
  }
}
